body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  top: 0;
  padding: 1.2rem;
  display: flex;
  flex-direction: row;
  margin-bottom: -1rem;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  background: #FFF;
  background-image: none;
}

/* Custom Select */
.header__select {
  display: flex;
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;
  position: relative;
  text-align: center;
  display: block;
  line-height: 3;
  margin-left: -2px;
  background: #FFF;
  border: 2px solid #e0e0e0;
  overflow: hidden;
  border-radius: .25em;
}

select {
  width: 100%;
  height: 100%;
  margin: 0;
  color: #121212;
  cursor: pointer;
}
select::-ms-expand {
  display: none;
}
/* Arrow */
.header__select::after {
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 0 1em;
  pointer-events: none;
}
/* Transition */
.header__select:hover::after {
  color: #f39c12;
}
.header__select::after {
  -webkit-transition: .25s all ease;
  transition: .25s all ease;
}
.horario-card {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 6px 18px 0px;
  background-color: none;
  margin-top: 0.6rem;
  margin-bottom: 0.6rem;
  border-radius: 6px;
  
  -webkit-transition: all 0.3s;
  
  transition: all 0.3s;
}

.horario-card:hover {
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 6px 0px;
  background-color: #DDD;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.horario-card__horario{
  font-weight: bold;
  font-size: 1.1rem;
  color: black;
  margin: 12px;
}

.horario-card__obs {
  color: crimson;
  font-size: 1.1rem;
  margin: 12px;
}
.container {
  margin: 1rem;
}
.section {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-left: 1rem;
}

.section__text {
  font-weight: bold;
  font-size: 1.2rem;
  color: #121212;
}
.horario-grande {
  flex: 0 0 auto;
  border: none;
  border-radius: 6px;
  width: 246px;
  height: 128px;
  color: #FFF;
  padding: 8px;
  margin: 1rem;
  -webkit-transition: box-shadow 0.3s ease-out;
  transition: box-shadow 0.3s ease-out;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 8px 0px;
}

.horario-grande:hover {
  box-shadow: rgba(0, 0, 0, 0.4) 0px 6px 18px 0px;
  -webkit-transition: box-shadow 0.3s ease-in;
  transition: box-shadow 0.3s ease-in;
}

.background-anglo-ru {
  background-image: -webkit-linear-gradient(top left, #0a0fad, #1bffff);
  background-image: linear-gradient(to bottom right, #0a0fad, #1bffff);
  -webkit-transition: background-image 0.3s ease-out;
  transition: background-image 0.3s ease-out;
}

.background-anglo-ru:hover {
  background-image: -webkit-linear-gradient(top left, #0a0fad, #1bffff) #0a0fad top;
  background-image: linear-gradient(to bottom right, #0a0fad, #1bffff) #0a0fad top;
  -webkit-transition: background-image 0.3s ease-in;
  transition: background-image 0.3s ease-in;
}

.background-anglo-med {
  background-image: -webkit-linear-gradient(top left, #D4145A, #FBB03B);
  background-image: linear-gradient(to bottom right, #D4145A, #FBB03B);
}

.background-anglo-esef {
  background-image: -webkit-linear-gradient(top left, #009245, #FCEE21);
  background-image: linear-gradient(to bottom right, #009245, #FCEE21);
}

.background-anglo-capao {
  background-image: -webkit-linear-gradient(top left, #312A6C, #852D91);
  background-image: linear-gradient(to bottom right, #312A6C, #852D91);
}

.background-capao-anglo {
  background-image: -webkit-linear-gradient(top left, #3A3897, #A3A1FF);
  background-image: linear-gradient(to bottom right, #3A3897, #A3A1FF);
}

.background-white {
  background-color: #FFF;
}

.horario-grande__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.horario-grande__header--linha {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 900;
  color: rgba(255, 255, 255, 0.7)
}

.horario-grande__header--observacao {
  font-size: 14px;
  font-weight: bold;
}

.horario-grande__horario-container {
  display: flex;
  padding-bottom: 8px;
}

.horario-grande__horario-container--horario {
  font-size: 68px;
  font-weight: bold;
  align-self: flex-end;
  margin-top: auto;
}
.proximos-horarios {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
}

.proximos-horarios:hover {
  flex-wrap: unset;
}
body {
  height: 100%;
}

.main-container {
  width: 100%;
  height: 100vh;
  background-color: #FFF;
}
.carregando {
  width: 100%;
  height: 100%;
}

.carregando__texto {
  text-align: center;
}
.App {
  width: 100vw;
}

